import styled from "styled-components";
import { lazy, Suspense } from "react";
import { motion } from "framer-motion";
import { ProjectsFull, ProjectsFramework, ProjectsBasic } from "./ProjectData";
import ProjectComponent from "./ProjectComponent";
import Loading from "../subComponents/Loading";
import { mediaQueries } from "./Themes";

const SocialIcons = lazy(() => import("../subComponents/SocialIcons"));
const HomeButton = lazy(() => import("../subComponents/HomeButton"));
const BigTitle = lazy(() => import("../subComponents/BigTitle"));

const MainContainer = styled(motion.div)`
  background-image: radial-gradient(#000 10%, transparent 11%),
    radial-gradient(#000 10%, transparent 11%);
  background-size: 10px 10px;
  background-position: 0 0, 30px 30px;
  background-repeat: repeat;
`;

const Container = styled.div`
  background-color: ${(props) => `rgba(${props.theme.bodyRgba},0.8)`};

  //width:100vw;
  width: 100%;
  height: auto;
  position: relative;
  padding-bottom: 5rem;
`;

const Center = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10rem;

  ${mediaQueries(30)`
    padding-top: 7rem;
    
  
  `};
`;

const Section = styled.h1`
    margin: 70px 0 20px 0;
`;

const Grid = styled(motion.div)`
  display: grid;
  grid-template-columns: repeat(2, minmax(calc(10rem + 15vw), 1fr));

  grid-gap: calc(1rem + 2vw);

  ${mediaQueries(50)`
    grid-template-columns: 100%;
  `};
`;

const container = {
    hidden: { opacity: 0 },
    show: {
        opacity: 1,
        transition: {
            staggerChildren: 0.5,
            duration: 0.5,
        },
    },
};

const ProjectPage = () => {


    return (
        <Suspense fallback={<Loading />}>
            <MainContainer
                variants={container}
                initial="hidden"
                animate="show"
                exit={{ opacity: 0, transition: { duration: 0.5 } }}
            >
                <Container>

                    <HomeButton />

                    <SocialIcons />

                    <Center>
                        <Section>Aplicações completas</Section>
                        <Grid variants={container} initial="hidden" animate="show">
                            {ProjectsFull.map((project) => (
                                <ProjectComponent key={project.id} project={project} />
                            ))}
                        </Grid>
                        <Section>Frameworks</Section>
                        <Grid variants={container} initial="hidden" animate="show">
                            {ProjectsFramework.map((project) => (
                                <ProjectComponent key={project.id} project={project} />
                            ))}
                        </Grid>
                        <Section>HTML, CSS, JS</Section>
                        <Grid variants={container} initial="hidden" animate="show">
                            {ProjectsBasic.map((project) => (
                                <ProjectComponent key={project.id} project={project} />
                            ))}
                        </Grid>
                    </Center>
                    <BigTitle text="projetos" top="-1rem" left="5rem" />
                </Container>
            </MainContainer>
        </Suspense>
    );
};

export default ProjectPage;
