import styled from "styled-components";

const Container = styled.div`
  background-color: #FCF6F4;
  width: 400px;
  height: auto;
  border: solid 1px black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;

  @media (max-width: 500px) {
    width: 80vw;
    height: auto;
  }

`;

const Body = styled.div`
  @media (max-width: 500px) {
    margin: 0 0 25px 0;
  }

`;

const Img = styled.img`
  width: 100%;
  height: 185px;
  cursor: pointer;

  @media (max-width: 550px) {
    height: 34vw;
  }
`;

const Footer = styled.div`
  margin: 10px 0 0 0;
`;

const ProjectComponent = (props) => {
    const { name, tags, description, fronturl, backurl, imgSrc, link } = props.project;
    return (
        <Container>
            <Body>
                <Img src={imgSrc} onClick={() => { window.location = link }}></Img>
                <h2>{name}</h2>
                <p>{description}</p>
            </Body>
            <Footer>
                <hr />
                <p>{tags}</p>
                {backurl ?
                    <div>
                        <a href={fronturl}>Repositório Frontend</a>
                        <br />
                        <a href={backurl}>Repositório Backend</a>
                    </div> :
                    <div>
                        <a href={fronturl}>Repositório Frontend</a>
                    </div>}
            </Footer>
        </Container>
    );
};

export default ProjectComponent;
